<template>
  <div>
    <h4></h4>
    <div class="grid col-2">
      <VueFormField title="">
        <span>
          <VueButton class="primary" @click="openService()"></VueButton> 
        </span>
      </VueFormField>
      <VueFormField title="">
        <span>
          <VueButton class="primary" @click="openComponents()"></VueButton>
        </span>
      </VueFormField>
    </div>
    <h4></h4>
    <div class="grid col-2">
      <VueFormField subtitle="" title=" mixin">
        <span>
          <VueButton class="primary" @click="openPageMixin()"></VueButton>
        </span>
      </VueFormField>
      <VueFormField subtitle="" title="script:add">
        <span>
          <VueButton class="primary" @click="seeDoc()"></VueButton>
        </span>
      </VueFormField>
    </div>
    <h4></h4>
    <div class="grid col-2">
      <VueFormField title="UI ">
        <template v-if="!uiIsNew">
          <span>
            <VueButton class="danger" icon-left="info" @click="updateUI()"></VueButton>UI  package.json 
          </span>
        </template>
        <template v-else>
          <span>
            UI  package.json 
          </span>
        </template>
      </VueFormField>
      <VueFormField title="Vue ">
        <span>
           Vue  `npm run dll`
          <VueButton class="primary" @click="updateDLL()"></VueButton>
        </span>
      </VueFormField>
    </div>
  </div>
</template>
<script>
import config from '../../config.json';
export default {
  sharedData() {
    return {
      ...mapSharedData(`${config.prefix}.`, {
        pages: "pages",
        uiIsNew: "uiIsNew",
        root: "root"
      })
    };
  },
  methods: {
    updateUI(force) {
      this.$router.push(
        `/tasks/${encodeURIComponent(this.root)}:update:ui${
          force ? ":force" : ""
        }`
      );
    },
    updateDLL() {
      this.$router.push(`/tasks/${encodeURIComponent(this.root)}:dll`);
    },
    open(module) {
      this.$callPluginAction(`${config.prefix}.open`, {
        module
      });
    },
    openPageMixin() {
      this.open("pageMixin");
    },
    openService() {
      this.open("service");
    },
    openComponents() {
      this.open("components");
    },
    seeDoc() {
      this.$emit("docs", "/guide/advanced/template.html");
    }
  }
};
</script>
